import fragmentSource from "!!ts-shader-loader!./fragment.glsl";
import vertexSource from "!!ts-shader-loader!./vertex.glsl";
import { create, tsx } from "@dojo/framework/core/vdom";

import GLPanel from "../../dojo-gl/widgets/GLPanel";
import * as css from "./Cube.m.css";

export interface CubeProperties {
    onGLReady: (glPanel: GLPanel) => void;
}

const factory = create().properties<CubeProperties>();

export default factory(function CubePanel({ properties }) {
    const { onGLReady } = properties();
    return (
        <div classes={[css.root]}>
            <GLPanel fragmentSource={fragmentSource} vertexSource={vertexSource} onGLReady={onGLReady} />
        </div>
    );
});
